import http from '../../../../../configs/httpConfig'
import {notifySuccessadd, notifyError} from '../../../../Toast'

export const clearData = () => {
  return {type:'CLEAR_DATA'}
}


export const getdatainit = () => ({
  type: 'GET_ALL_REPORTS_INIT'
})

export const getdownload = () => ({
  type: 'SET_DOWNLOAD_ON'
})

export const getAllContests = () => {
  return async (dispatch) => {
    dispatch(getdatainit())
    await http.get('/admin_contests/list').then((response) => {
      dispatch({
        type: 'GET_ALL_CONTESTS',
        data: response.data.data
      })
    })
  }
}

export const getContestsListPDF = () => {
  return async () => {
    await http
      .get('/admin_contests/export_pdf', { responseType: 'blob' })
      .then((response) => {
        notifySuccessadd('Successfully Downloaded')
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', `contests_${new Date().valueOf()}.pdf`) //or any other extension
        document.body.appendChild(link)
        link.click()
      })
      .catch(() => {
        notifyError('Download failed')
      })
  }
}

