export const getreferralreportdownload = (from = '', to = '') => {
  return async (dispatch) => {
   
    dispatch(getdownload())
    await http
      .get(from && to ? `/reports/referralReport?from=${from}&to=${to}&download=true` : '/reports/referralReport?download=true', { responseType: 'blob' })
      .then((response) => {
        notifySuccessadd('Successfully Downloaded')
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', 'Referralreport.csv') //or any other extension
        document.body.appendChild(link)
        link.click()
        dispatch({
          type: 'SET_DOWNLOAD_OFF'
        })
      })
      .catch(() => {
        dispatch({
          type: 'SET_DOWNLOAD_OFF'
        })
        notifyError('Download failed')
      })
  }
}

