  export const togglestatus = (id) => {
    return async (dispatch) => {
      ///dispatch(getAllbannerDataInit())      
      await http.post(`/redeem/toggle_active/${id}`).then((response) => {   
        notifySuccessadd(response.data.message)                  
        dispatch({
          type: 'BANNER_TOGGLE_STATUS',
          data: response
        })  
        
      }).catch((e) => {
        notifyError(e.message)
        console.log("Error in Feed Post: ", e)
        
      })
    }
  }
  

